import React, { Component } from 'react';
import PoolBack from '../../Images/poolback.jpeg';
import './Register.css';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import Input from '@material-ui/core/Input';
import Button from '@material-ui/core/Button';
import { makeStyles } from '@material-ui/core/styles';
import MenuItem from '@material-ui/core/MenuItem';
import FormHelperText from '@material-ui/core/FormHelperText';
import Select from '@material-ui/core/Select';
import axios from 'axios';
import {Redirect} from "react-router-dom";
import Config from '../../config/config';
import {signup,isAuth} from '../../actions/auth';


export default class Register extends Component {
  constructor(props, context) {
    super(props, context);
    this.state = {
      username: '',
      usernameE: false,
      password: '',
      passwordE: false,
      mail:'',
      mailE:false,
      mailEmsg:'',

      error:''
    }

    this.handelSubmit = this.handelSubmit.bind(this);
    this.handleChange = this.handleChange.bind(this);

  }
  

  handelSubmit() {
   
    this.setState({mailE:false})
    this.setState({usernameE:false})
    this.setState({passwordE:false})



    let error=false;
    if(this.state.username===''){
      this.setState({usernameE:true})
    error=true;
    }


    if(this.state.mail===''){
    this.setState({mailEmsg:" "})
    this.setState({mailE:true})
    error=true;

    }else  if(!this.state.mail.includes('@')){
      this.setState({mailEmsg:"  "})
      this.setState({mailE:true})
    error=true;
    }
    if(this.state.password===''){
      this.setState({passwordE:true})
      error=true;
      }
   

    if(error) return
    const postData = {
      email: this.state.mail.trim(),
      password: this.state.password,
      name:this.state.username,
  };
  signup(postData)
    .then(res => {
if(res.data.status==='faild'){
  this.setState({mailEmsg:"  "})
  this.setState({mailE:true})
return
}

      this.props.setUser(res.data.user)

    })
    .catch(() => {}   );
    console.log('send')
  }
   handleChange(event) {
    this.setState({userType:event.target.value});
  };

  render() {
    if(isAuth())
    return <Redirect to={'/UserDashboard'}/>;
    return (
      <div className='Register' >
{/* {this.handelSubmit()} */}
        <h1 className='h1-register'>    <span className='summer_txt-register'>  </span></h1>
        <p id='pText-register' className='h1-register'> </p>

        <div  id='register-form' >
          <FormControl className='login'  >
            <InputLabel id="input-user" htmlFor="input-user"  > </InputLabel>
            <Input required type='text' error={this.state.usernameE} id="input-user" aria-describedby="my-helper-text" value={this.state.username} onChange={(e) => this.setState({ username: e.target.value })} />
            {this.state.usernameE?  <FormHelperText  error={this.state.usernameE} id="helper">  </FormHelperText>:''}

          </FormControl>

          <br />
          <FormControl  >

<InputLabel id="input-mail" htmlFor="input-mail"></InputLabel>

<Input required type='email' error={this.state.mailE}  id="input-mail" aria-describedby="my-helper-text" value={this.state.mail} onChange={(e) => this.setState({ mail: e.target.value })} />
    {this.state.mailE?  <FormHelperText  error={this.state.mailE} id="helper">{this.state.mailEmsg}</FormHelperText>:''}

</FormControl>
<br/>

          <FormControl>

            <InputLabel id="input-pass" htmlFor="input-pass"></InputLabel>

            <Input id="input-pass" error={this.state.passwordE} required type='password' id="input-pass" aria-describedby="my-helper-text" value={this.state.password} onChange={(e) => this.setState({ password: e.target.value })} />
            {this.state.passwordE?  <FormHelperText  error={this.state.passwordE} id="helper">  </FormHelperText>:''}

          </FormControl>

          <br/>
        
          {/* <FormControl  variant="standard" id='userType-select'>
        <InputLabel shrink id='userType-select-label'> </InputLabel>
        <Select
        required
        labelId="demo-simple-select-placeholder-label-label"
        id="demo-simple-select-placeholder-label"
          value={this.state.userType}
          onChange={this.handleChange}
          displayEmpty
        >
    <MenuItem value="">
            <em></em>
          </MenuItem>
          <MenuItem value={20}></MenuItem>
        </Select>
      </FormControl> */}
          <button  onClick={this.handelSubmit} id='register-submit'  type="submit" value="Submit" variant="outlined" color="primary"  >
            
    </button>
<br/>


    <a href='/' className='login-link'> ?  </a>

        </div>
      </div>

    );
  }
}
