import React, { Component,Fragment } from 'react';
import './NewFoodMenuEvent.css';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import Input from '@material-ui/core/Input';
import FormHelperText from '@material-ui/core/FormHelperText';
import Button from '@material-ui/core/Button';
import { Redirect } from "react-router-dom";
import DatePicker from "react-datepicker";
import UserDashboardNav from '../UserDashboardNav/UserDashboardNav';
import Event from './Event';
import { create } from 'jss';
import rtl from 'jss-rtl';
import { StylesProvider, jssPreset } from '@material-ui/core/styles';
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';
import TextareaAutosize from '@material-ui/core/TextareaAutosize';
import TextField from '@material-ui/core/TextField';
import Config from '../../config/config';
import axios from 'axios';
import "react-datepicker/dist/react-datepicker.css";
import { registerLocale, setDefaultLocale } from  "react-datepicker";
import es from 'date-fns/locale/es';
registerLocale('es', es)
export default class NewFoodMenuEvent extends Component {
  constructor(props, context) {
    super(props, context);
    this.state = {
  
      exit: false,


      updateMode:false,
      startDate:new Date(),
      EventsBreakfast:[],
      EventsLunch:[],
      EventsAfternoon:[],

      add:false,
      menu:'',
      menuE:false,

      discription:'',
      discriptionE:false,

      menuUse:[],

    }

    this.handleClick = this.handleClick.bind(this);
    this.handleClose = this.handleClose.bind(this);
    this.handleChangeSelectMenu = this.handleChangeSelectMenu.bind(this);

    this.addEvet = this.addEvet.bind(this);
    this.openNew = this.openNew.bind(this);
    this.handleAddEvent = this.handleAddEvent.bind(this);
    this.ClearError = this.ClearError.bind(this);



  }
  componentDidMount(){
    if(this.props.location.update){

      this.setState({updateMode:true})
      let date=this.props.location.date
      let d=new Date(date);
      this.setState({startDate:d})
    
   
    }
  }
  handleAddEvent(){

   
    let itemMenu=[];
    this.state.EventsBreakfast.map((item,index)=>{
      console.log(item)
      itemMenu.push(item);

    })
    this.state.EventsLunch.map((item,index)=>{
      itemMenu.push(item);

    })
    this.state.EventsAfternoon.map((item,index)=>{
      itemMenu.push(item);

    })
   

    console.log(itemMenu)

    const postData = {
      date: this.state.startDate,
      foods: itemMenu,
  };
    axios.post(Config.getServerPath()+'menu',postData)
    .then(res => {
if(res.data.status===400){
  console.log('error')
return
}
console.log(res.data)
this.props.updateMenuEvent(res.data.day);

this.setState({ exit: true });


    })
    .catch(() => {    console.log('send')
  }   );



  }
  ClearError(){

    this.setState({discriptionE:false})
    this.setState({menuE:false})
 


  }
  addEvet() {
    this.ClearError();
    let error=false;

    if(this.state.discription==''){
      this.setState({discriptionE:true})
      error=true
      
    }
    if(this.state.menu==''){
      this.setState({menuE:true})
      error=true
    }
  

    if(error) return

    let item=[];
    if(this.state.menu==10)
     item = this.state.EventsBreakfast;
    if(this.state.menu==20)
     item = this.state.EventsLunch;

    if(this.state.menu==30)
     item = this.state.EventsAfternoon;

    const discription = this.state.discription;
    const category = this.state.menu;

    item.push({ discription, category})
    // use.push(this.state.startTime)
    if(this.state.menu==10)
    this.setState({ EventsBreakfast: item })
    if(this.state.menu==20)
    this.setState({ EventsLunch: item })

    if(this.state.menu==30)
    this.setState({ EventsAfternoon: item })

    this.setState({discription:''})
    this.setState({menu:''})


  }
  
  handleClick = (event) => {
    this.setState({ anchorEl: event.currentTarget });
  };

  handleClose = () => {
    this.setState({ exit: true });

  };
  handleChangeSelectMenu(event) {
    this.setState({ menu: event.target.value });

  }
 
  openNew(){

    this.setState({add:!this.state.add});
  }


  render() {
    if(this.props.user.type!=='0'&&this.props.user.type!=='2')
    return <Redirect to={'/'}/>;
    if (this.state.exit)
      return <Redirect to={'/FoodMenu'} />;
    return (

      <div className='new-day-event' dir="rtl">

        <h3 className='titel-new-event'><u>  </u> </h3>
        <DatePicker disabled={this.state.updateMode} id='date-piker'   dateFormat="dd/MM/yy" selected={this.state.startDate} onChange={(date) => this.setState({startDate: date})} />
        <button  onClick={this.openNew} className='menu-add-btn'>  </button>
        {this.state.add?(
<div id='form-event-div'>
        <FormControl id='form-event' >

          <InputLabel id="child" htmlFor="child-name"></InputLabel>

          <Input aria-describedby="helper"  error={this.state.discriptionE} required type='text' id="child-name" value={this.state.discription} onChange={(e) => this.setState({ discription: e.target.value })} />
          {this.state.discriptionE?  <FormHelperText  error={this.state.discriptionE} id="helper"> </FormHelperText>:''}

        </FormControl>

        <br />
        <div style={{display:'flex'}}>
       
        <FormControl  variant="standard" id='select-menu'>
        <InputLabel error={this.state.menuE} shrink id='child'> </InputLabel>

        <Select
        required
        labelId="demo-simple-select-placeholder-label-label"
        id="demo-simple-select-placeholder-label"
          value={this.state.menu}
          onChange={this.handleChangeSelectMenu}
          displayEmpty
        >
      

       <MenuItem id='val'  value={10}> </MenuItem>
       <MenuItem id='val' value={20}> </MenuItem>
       <MenuItem id='val' value={30}>  </MenuItem>

          
        </Select>
      </FormControl>
      
      </div>
      <button onClick={this.addEvet} className='event-add-btn'></button>
      </div>
      ):''}
        <br />
        <div className='menu-type'>
         {this.state.EventsBreakfast.length!=0?( <p className='menu-type-title'><u>  <span class="iconify" data-icon="whh:restaurantmenu" data-inline="false"></span>   </u></p>):''}
        { this.state.EventsBreakfast.map((item,index)=>{
          return  <Event key={index} event={item} big={true}/>

        })}
        </div>
        <div className='menu-type'>
         {this.state.EventsLunch.length!=0?( <p className='menu-type-title'><u>   <span class="iconify" data-icon="whh:restaurantmenu" data-inline="false"></span>   </u></p>):''}
            { this.state.EventsLunch.map((item,index)=>{
          return  <Event key={index} event={item} big={true}/>

        })}
        </div>
        <div className='menu-type'>
         {this.state.EventsAfternoon.length!=0?( <p className='menu-type-title'><u>  <span class="iconify" data-icon="whh:restaurantmenu" data-inline="false"></span>    </u></p>):''}
            { this.state.EventsAfternoon.map((item,index)=>{
          return  <Event key={index} event={item} big={true}/>

        })}
      
      </div>
        <Button onClick={this.handleAddEvent} id='child-submit-event' ></Button>
        <Button onClick={this.handleClose} id='child-submit-event-cancel' ></Button>

      </div>

    );
  }
}
